import React from 'react';
import PropTypes from 'prop-types';

const joinSitiesName = cities => cities.reverse().reduce((string, city) => `${string} ${city.cityName}`, '');

const Results = (props) => {
  const newGame = () => {
    props.finishGame();
    props.closeModal();
  };

  const userCitiesCount = props.userCities.items.length;
  const computerCitiesCount = props.computerCities.items.length;

  return (
    <div className="results">
      <h2>
        {
          userCitiesCount === computerCitiesCount ?
          ' ' :
          ' !'
        }
      </h2>
      <div>
        <p>  : {computerCitiesCount + userCitiesCount}</p>
        <p>  : {userCitiesCount}</p>
        <p>  : {joinSitiesName(props.userCities.items)}</p>
        <p>  : {joinSitiesName(props.computerCities.items)}</p>
      </div>
      <button className="btn btn--primary btn--new-game" onClick={newGame}> </button>
    </div>
  );
};

Results.propTypes = {
  finishGame: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  computerCities: PropTypes.shape({
    items: PropTypes.array,
  }).isRequired,
  userCities: PropTypes.shape({
    items: PropTypes.array,
  }).isRequired,
};

export default Results;



